.program-list-item-title{
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 1.29;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    color: $white-color;
    margin-top: 0px;
    &::before{
    content: "";
    position: absolute;
    display: block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: $accent-color;
    transform: translateX(-33px) translateY(0px);
     
    
    }
}

    .our-program{
        padding-left: 74px;
    }
    
    
    .caption-name{
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 1.64;
    
    letter-spacing: 0.08em;
    color: $accent-color;
    margin-bottom: 15px;
     text-align: start;}

    .caption-description-span{
        display: block;
    }
    .program-list-item {
      padding-bottom: 43px;
      padding-left: 26px;
     

      &:not(:last-child){
        border-left: 2px solid #FFFFFF;
              }
         &:last-child{
             padding-left: 28px;
         }     
    }

    
    .caption-list-item:not(:last-child){
        margin-bottom: 13px;
    }
    .caption-description{
        margin-bottom: 5px;    
        font-style: normal;
      font-weight: 500;
      font-size: 12px;
      line-height: 1.67;
       letter-spacing: 0.08em; 
       color: #B9B9B9;;  
    }
    
            
    .caption-name{
        margin-left: 10px;
    &::before{
        content: "";
        transform: translateY(-4.5px);
        display:inline-block ;
        margin-right: 10px;
        text-align: center;
        width: 13px;
        height: 1px;
        background-color: $accent-color;
    }}
    .program-list{
        position: relative;
      
        margin-left: auto;
        margin-right: auto;}
        
           
    
         
        .title-w{
           color: $white-color;
           text-align: center;
           margin-bottom: 43px;
        }

        .section{
            padding: 50px 0px;
        }
        .img-layout{
            background-image: url('../images/mobile/our-team-mobile.png');
            background-repeat: no-repeat;
            background-position: right  bottom ;

        }     
        .our-program{
            
            background:linear-gradient($title-text-color 87% , $white-color 13%);
             
             
              
        }
        .lyout-img{
            background-image: url('../images/mobile/our-team-mobile.png');
            background-repeat: no-repeat;
            background-position: right  bottom 670px;}
        
        .choice-title{
            
            margin-bottom: 30px;
            margin-top: 0px;
            color: $white-color;
            font-weight: 800;
            font-size: 20px;
            line-height: 1.35;
            letter-spacing: 0.03em;
        }
        .choice-number{
            display: block;
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 1.64;
            letter-spacing: 0.08em;
            color: $accent-color;
            margin-bottom: 10px;
            
              
            &::before{
                content: "";
                transform: translateY(-4.5px);
                display:inline-block ;
                margin-right: 10px;
                text-align: center;
                width: 13px;
                height: 1px;
                background-color: $accent-color;
              }
              
        }

.choice-list-item{
    margin-bottom: 20px;
}
.choise-desciption{
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 1.43;
    color: $title-text-color;
}
        
.choice-container{
    padding: 20px 30px;
    background-color: $white-color;
    box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);
}